<template>
  <layout-default>
    <div class="page-content">
      <div class="container-fluid">

          <!-- start page title -->
          <div class="row">
              <div class="col-12">
                  <div class="page-title-box d-flex align-items-center justify-content-between">
                      <h4 class="mb-0">جدول المصروفات</h4>
                      <div class="page-title-right">
                          <form-modal ref="FormModal" :fetchData="fetchData"/>
                          <pay-modal ref="PayModal" :fetchData="fetchData"/>
                      </div>
                  </div>
              </div>
          </div>
          <!-- end page title -->
          
          <div class="row">
              <div class="col-12">
                  <div class="card">
                      <div v-if="client_payments&&client_payments.data.length" class="card-body">
                          <table id="datatable" class="table table-bordered dt-responsive nowrap" style="border-collapse: collapse; border-spacing: 0; width: 100%;">
                              <thead>
                              <tr>
                                  <th>رقم الفاتورة</th>
                                  <th>أسم العميل</th>
                                  <th>تليفون</th>
                                  <th>رقم القسط</th>
                                  <th>المبلغ المستحق</th>
                                  <th>تاريخ الأستحقاق</th>
                                  <th>خيارات</th>
                              </tr>
                              </thead>
                              <tbody>
                              <tr v-for="(client_payment,index) in client_payments.data">
                                    <td>
                                        <router-link :to="{name:'client_orders_show', params: { order_id: client_payment.order.id }}" tag="a" href="javascript:;">
                                            #{{client_payment.order.id}}{{client_payment.order.created_at| moment("DDMMYY")}}
                                        </router-link>
                                    </td>
                                    <td>{{client_payment.order.name}}</td>
                                    <td>{{client_payment.order.phone}}</td>
                                    <td>{{client_payment.number}}</td>
                                    <td>{{moneyFormat(client_payment.amount,0)}} L.E</td>
                                    <td>{{client_payment.pay_at| moment("DD-MM-YYYY")}}</td>
                                    <td>
                                        <router-link :to="{name:'order_payment', params: { payment_id: client_payment.id }}" tag="a" class="btn p-0 text-warning">
                                            <i class="far fa-eye"></i> <b>رؤية</b>
                                        </router-link>
                                        <span>|</span>
                                         <a @click="$refs.PayModal.edit(client_payment)" class="btn p-0 text-info" href="javascript:;">
                                            <i class="far fa-edit"></i> <b>تعديل</b>
                                        </a>
                                        <span>{{(client_payment.amount>0)?'|':''}}</span>
                                        <a v-if="client_payment.amount>0" @click="$refs.PayModal.payMoney(client_payment)" class="btn p-0 text-success" href="javascript:;">
                                            <i class="fas fa-money-bill-wave"></i> <b>سداد</b>
                                        </a>
                                    </td>
                              </tr>
                              </tbody>
                          </table>
                          <pagination-nav v-if="client_payments.last_page>1" :pageSize="5" :currentPage="2" :GoToPage="GoToPage" />
                      </div>
                      <div v-else class="card-body">
                          <beat-loader v-if="loader" :loading="loader" color="#5578eb"></beat-loader>
                          <p v-else class="card-title-desc" style="text-align: center;">
                            لا يوجد بيانات مضافة حتى الآن
                          </p>
                      </div>
                  </div>
              </div> <!-- end col -->
          </div> <!-- end row -->
      </div> <!-- container-fluid -->
    </div>
  </layout-default>
</template>  


<script>
import LayoutDefault from '../../layouts/App';
import PaginationNav from '../../components/pagination';
import FormModal from '../../components/modals/payment/add';
import PayModal from '../../components/modals/client/pay';

import axios from 'axios';

export default {
  components: {LayoutDefault,PaginationNav,FormModal,PayModal},
  data() {
        return {
            client_payments:null,loader:false
        }
    },  
  created() { 
    this.fetchData();
  },
  methods: {
    fetchData(num=1) {
      this.client_payments=null;
      this.loader=true;
      let data = {params: {page:num}};
      axios.get('/api/all_late_payments',data).then(({data}) => {
          this.client_payments=data.success.client_payments;
          if(!data.success.client_payments.data.length&&num!=1){
              this.fetchData(1)
          }           
          this.loader=false;
      });  
    },
  },
}
</script>